import logo from "./shop_logo.svg";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";

import { Routes, Route, Link, Navigate, useLocation } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart as farHeart } from "@fortawesome/free-regular-svg-icons";
import {
  faCartShopping,
  faMagnifyingGlass,
} from "@fortawesome/free-solid-svg-icons";
import Accessories from "./page/Accessories";
import Swimsuits from "./page/Swimsuits";
import Rashguards from "./page/Rashguards";
import Event from "./page/Event";
import EventList from "./page/EventList";
import Detail from "./page/Detail";
import Home from "./page/Home";
import Search from "./page/Search";
import Footer from "./Footer";

import data from "./data";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

function App() {
  const location = useLocation();
  return (
    <div className="App">
      {" "}
      <Navbar collapseOnSelect expand="lg" className="bg-body-tertiary">
        <Container>
          <Navbar.Brand as={Link} to="/" className="logo">
            <img src={logo} className="img" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link
                as={Link}
                to="/rashguards"
                className={
                  location.pathname.includes("rashguards")
                    ? "link active"
                    : "link"
                }
              >
                래쉬가드
              </Nav.Link>
              <Nav.Link
                as={Link}
                to="/swimsuits"
                className={
                  location.pathname.includes("swimsuits")
                    ? "link active"
                    : "link"
                }
              >
                수영복
              </Nav.Link>
              <Nav.Link
                as={Link}
                to="/accessories"
                className={
                  location.pathname.includes("accessories")
                    ? "link active"
                    : "link"
                }
              >
                액세서리
              </Nav.Link>
            </Nav>
            <Nav>
              <Nav.Link
                as={Link}
                to="/event"
                className={
                  location.pathname.includes("event") ? "link active" : "link"
                }
              >
                이벤트
              </Nav.Link>
              <Nav.Link as={Link} to="/search">
                <FontAwesomeIcon icon={faMagnifyingGlass} className="icon" />
              </Nav.Link>
              <Nav.Link href="#deets">
                <FontAwesomeIcon icon={farHeart} className="icon" />
              </Nav.Link>
              <Nav.Link href="#deets">
                <FontAwesomeIcon icon={faCartShopping} className="icon" />
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <Routes>
        <Route path="/" element={<Home />}></Route>
        <Route path="/rashguards" element={<Rashguards />}></Route>
        <Route path="/swimsuits" element={<Swimsuits />}></Route>
        <Route path="/accessories" element={<Accessories />}></Route>
        <Route path="/event" element={<Event />}>
          <Route
            path="ingEvent"
            element={<EventList events={data.eventing} />}
          />
          <Route
            path="endEvent"
            element={<EventList events={data.eventend} />}
          />
          <Route
            path="allEvent"
            element={
              <EventList events={[...data.eventing, ...data.eventend]} />
            }
          />
          <Route path="/event" element={<Navigate to="ingEvent" />} />
        </Route>
        <Route path="/product/:id" element={<Detail data={data} />}></Route>
        <Route path="/search" element={<Search data={data} />}></Route>
      </Routes>
      <Footer></Footer>
    </div>
  );
}

export default App;
