import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
import Item from "../Item";
import data from "../data";

const Search = () => {
  const navigate = useNavigate(); // useHistory 훅 사용
  const [query, setQuery] = useState(""); // 사용자 입력을 저장하는 상태
  const [searchInitiated, setSearchInitiated] = useState(false);
  const [searchResults, setSearchResults] = useState([]); // 검색 결과를 저장

  const searchChange = (e) => {
    if (searchInitiated) {
      setSearchInitiated(false); // 사용자가 새로 입력을 시작하면 검색 시작 상태를 초기화
    }
    setQuery(e.target.value);
  };

  const performSearch = () => {
    setSearchInitiated(true); // 검색 시작 표시
    if (query.trim()) {
      const allItems = Object.values(data).flat();
      const normalizedQuery = query.replace(/\s+/g, "").toLowerCase(); // 공백 제거 및 소문자 변환
      const filteredData = allItems.filter((item) =>
        item.title.replace(/\s+/g, "").toLowerCase().includes(normalizedQuery)
      );
      setSearchResults(filteredData); // 검색 결과 저장
    }
    setQuery(""); // 검색 후 입력 필드 초기화
  };

  const searchKey = (e) => {
    if (e.key === "Enter") {
      performSearch();
    }
  };

  const searchButton = (e) => {
    e.preventDefault();
    performSearch();
  };
  const closeSearch = () => {
    navigate(-1); // '닫기' 클릭 시 이전 페이지로 이동
  };
  return (
    <div className="searchWrap">
      <div className="searchInput">
        <FontAwesomeIcon
          icon={faMagnifyingGlass}
          className="searchIcon"
          onClick={searchButton}
        />
        <form onSubmit={searchButton}>
          <input
            type="text"
            value={query}
            placeholder="검색어를 입력해주세요"
            onChange={searchChange}
            onKeyDown={searchKey}
          />
        </form>
        <span onClick={closeSearch}>닫기</span>
      </div>
      <div className="searchResult">
        {searchInitiated && searchResults.length === 0 && (
          <p>검색 결과가 없습니다.</p>
        )}
        {searchResults.length > 0 &&
          searchResults.map((item) => <Item key={item.id} item={item} />)}
      </div>
    </div>
  );
};

export default Search;
