import React from "react";
import data from "../data";
import Item from "../Item";

const Rashguards = () => {
  const rashguards = data.rashguards
    ? data.rashguards.filter((item) => item.product === "rashguard")
    : [];

  return (
    <div className="conWrap">
      <h2>rashguards</h2>
      <div className="conitemWrap">
        {rashguards.map((item) => {
          return <Item item={item} key={item.id}></Item>;
        })}
      </div>
    </div>
  );
};

export default Rashguards;
