import React from "react";
import Nav from "react-bootstrap/Nav";
import { Link, Outlet, useLocation } from "react-router-dom";

const Event = () => {
  const location = useLocation();
  return (
    <div>
      {" "}
      <Nav variant="pills" defaultActiveKey="/event" className="eventNav">
        <Nav.Item>
          <Nav.Link
            as={Link}
            to="ingEvent"
            className={
              location.pathname.includes("ingEvent") ? "link active" : "link"
            }
          >
            진행중인 이벤트
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link
            as={Link}
            to="endEvent"
            className={
              location.pathname.includes("endEvent") ? "link active" : "link"
            }
          >
            종료된 이벤트
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link
            as={Link}
            to="allEvent"
            className={
              location.pathname.includes("allEvent") ? "link active" : "link"
            }
          >
            전체
          </Nav.Link>
        </Nav.Item>
      </Nav>
      <Outlet></Outlet>
    </div>
  );
};

export default Event;
