import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart as farHeart } from "@fortawesome/free-regular-svg-icons";
import { faArrowUpFromBracket } from "@fortawesome/free-solid-svg-icons";

const Detail = ({ data }) => {
  const { id } = useParams();
  const [product, setProduct] = useState(null);
  const [selectedSizes, setSelectedSizes] = useState([]);
  const [selectedSize, setSelectedSize] = useState("");
  const [totalPrice, setTotalPrice] = useState(0);

  const findProductById = (data, id) => {
    for (const category in data) {
      const product = data[category].find((item) => item.id === id);
      if (product) return product;
    }
    return null;
  };

  useEffect(() => {
    const productFound = findProductById(data, id);
    if (productFound) {
      setProduct(productFound);
    }
  }, [id, data]);

  const handleSizeSelect = (size) => {
    setSelectedSize(size); // 선택된 사이즈 상태 업데이트
    if (selectedSizes.find((item) => item.size === size)) return; // 이미 선택된 사이즈는 무시
    const newItem = {
      title: product.title,
      color: product.color,
      size,
      quantity: 1,
      price: parseInt(product.price.replace(/,/g, "")),
    };
    setSelectedSizes([...selectedSizes, newItem]);
    updateTotalPrice([...selectedSizes, newItem]);
  };

  const handleQuantityChange = (index, delta) => {
    const newSizes = [...selectedSizes];
    newSizes[index].quantity = Math.max(1, newSizes[index].quantity + delta);
    setSelectedSizes(newSizes);
    updateTotalPrice(newSizes);
  };

  const handleRemoveItem = (index) => {
    const newSizes = selectedSizes.filter((_, idx) => idx !== index);
    setSelectedSizes(newSizes);
    updateTotalPrice(newSizes);
  };

  const updateTotalPrice = (sizes) => {
    const total = sizes.reduce(
      (acc, item) => acc + item.price * item.quantity,
      0
    );
    setTotalPrice(total);
  };

  if (!product) {
    return <div>제품을 찾을 수 없습니다.</div>;
  }
  return (
    <div className="detailWrap">
      <div className="detailImg">
        <div className="detailImgWrap">
          <img src={product.img} alt={product.title} />
          <img src={product.hoverImage} alt={product.title} />
        </div>
      </div>
      <div className="detailInfo">
        <h2>{product.title}</h2>
        <p>{product.color}</p>
        <h3>
          {product.price}원{" "}
          <span className="priceIcon">
            <FontAwesomeIcon icon={farHeart} />
            <FontAwesomeIcon icon={faArrowUpFromBracket} />
          </span>
        </h3>
        <ul>
          <li className="benefit">혜택내역</li>
          <li className="size">
            사이즈
            <div>
              {["080", "085", "090", "095"].map((size) => (
                <span
                  key={size}
                  onClick={() => handleSizeSelect(size)}
                  style={{
                    fontWeight: selectedSize === size ? "bold" : "normal",
                    cursor: "pointer",
                    border:
                      selectedSize === size
                        ? "2px solid black"
                        : "1px solid grey",
                  }}
                >
                  {size}
                </span>
              ))}
            </div>
          </li>
          {selectedSizes.map((item, index) => (
            <li key={index} className="buyItem">
              <div className="left">
                <p className="leftTitle"> {`${item.title}`}</p>
                <p> {`${item.color} / ${item.size} / ${item.price}`}</p>
              </div>
              <div className="right">
                <div className="dtBtn">
                  <button onClick={() => handleRemoveItem(index)}>삭제</button>
                </div>
                <div className="qutBtn">
                  <button onClick={() => handleQuantityChange(index, -1)}>
                    -
                  </button>
                  <span>{item.quantity}</span>
                  <button onClick={() => handleQuantityChange(index, 1)}>
                    +
                  </button>
                </div>
              </div>
            </li>
          ))}
          <li className="total">
            <span>총 상품 금액</span>{" "}
            <span>{totalPrice.toLocaleString()}원</span>
          </li>
          <li>
            <div className="priceBtn">
              <div className="saveBtn">장바구니</div>
              <div className="buyBtn">구매하기</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Detail;
