import React, { useRef } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleLeft, faAngleRight } from "@fortawesome/free-solid-svg-icons";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Autoplay } from "swiper/modules";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

const Swiper1 = () => {
  return (
    <div className="swiper-container">
      <Swiper
        slidesPerView={1}
        autoplay={{
          delay: 4000,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Pagination, Navigation, Autoplay]}
        loop={true}
        className="mySwiper"
      >
        <SwiperSlide>
          <img
            src={`${process.env.PUBLIC_URL}/imgs/main1.jpg`}
            alt=""
            className="img"
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src={`${process.env.PUBLIC_URL}/imgs/main2.jpg`}
            alt=""
            className="img"
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src={`${process.env.PUBLIC_URL}/imgs/main3.jpg`}
            alt=""
            className="img"
          />
        </SwiperSlide>
        <SwiperSlide>
          <img
            src={`${process.env.PUBLIC_URL}/imgs/main4.jpg`}
            alt=""
            className="img"
          />
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default Swiper1;
