import React from "react";
import data from "../data";
import Item from "../Item";

const Swimsuits = () => {
  const swimsuits = data.swimsuits
    ? data.swimsuits.filter((item) => item.product === "swimsuit")
    : [];
  return (
    <div className="conWrap">
      <h2>Swimsuits</h2>
      <div className="conitemWrap">
        {swimsuits.map((item) => {
          return <Item item={item} key={item.id}></Item>;
        })}
      </div>
    </div>
  );
};

export default Swimsuits;
