import React from "react";
import data from "../data";
import Swiper1 from "../Swiper1";
import Item from "../Item";

const Home = () => {
  return (
    <div>
      <Swiper1></Swiper1>

      <div className="mainBox1">
        <h2 className="title">이번 여름을 책임질 핫 아이템🔥</h2>
        <div className="itemWrap">
          {data.swimsuits.slice(0, 4).map((item) => (
            <Item key={item.id} item={item} />
          ))}
          {data.accessories.slice(0, 4).map((item) => (
            <Item key={item.id} item={item} />
          ))}
          {data.rashguards.slice(0, 4).map((item) => (
            <Item key={item.id} item={item} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Home;
